.bu-dash {
position: relative;
padding-left: 8px;
}
.bu-dash::before {
position: absolute;
top: 16px;
left: 0;
width: 4px;
height: 1px;
background-color: #000;
content: "";
}

.bu-dot {
position: relative;
padding-left: 10px;
}
.bu-dot::before {
position: absolute;
top: 12px;
left: 0;
width: 3px;
height: 3px;
background-color: #b0b0b0;
border-radius: 50%;
content: "";
}

.list li {
position: relative;
padding-left: 10px;
}
.list li::before {
position: absolute;
top: 12px;
left: 0;
width: 3px;
height: 3px;
background-color: #b0b0b0;
border-radius: 50%;
content: "";
}
.list li ul {
color: #757575;
}
.list li ul li {
position: relative;
padding-left: 8px;
}
.list li ul li::before {
position: absolute;
top: 16px;
left: 0;
width: 4px;
height: 1px;
background-color: #000;
content: "";
}
.list li ul li:before {
color: #757575;
}

