.datepicker {
z-index: 10000 !important;
}
.datepicker table {
table-layout: inherit;
}
.datepicker table td.today:hover {
background-color: #eeeeff !important;
}

.datepicker.dropdown-menu {
border: 2px solid #131547;
}

.datepicker.dropdown-menu th.next,
.datepicker.dropdown-menu th.prev {
border: 1px solid #131547;
border-radius: 3px;
font-size: 20px;
}

.datepicker.dropdown-menu .table-condensed {
border-collapse: separate;
}

.datepicker table tr td.today,
.datepicker .month.focused {
background-color: #131547;
color: #fff;
}
.datepicker table thead tr th {
font-size: 14px;
}
.datepicker table thead tr:last-child {
border-bottom: 1px solid #d9d9d9;
margin-bottom: 5px;
}

.datepicker-dropdown {
padding: 6px;
}

.datepicker-prev {
font-size: 21px;
}
.datepicker-prev::before {
content: "keyboard_arrow_left";
font-family: "Material Icons Outlined";
font-weight: normal;
font-style: normal;
display: inline-block;
line-height: 1;
vertical-align: text-bottom;
text-transform: none;
letter-spacing: normal;
word-wrap: normal;
white-space: nowrap;
-webkit-font-smoothing: antialiased;
text-rendering: optimizeLegibility;
-moz-osx-font-smoothing: grayscale;
font-feature-settings: "liga";
vertical-align: top;
margin-top: 5px;
}

.datepicker-next {
font-size: 21px;
}
.datepicker-next::before {
content: "keyboard_arrow_right";
font-family: "Material Icons Outlined";
font-weight: normal;
font-style: normal;
display: inline-block;
line-height: 1;
vertical-align: text-bottom;
text-transform: none;
letter-spacing: normal;
word-wrap: normal;
white-space: nowrap;
-webkit-font-smoothing: antialiased;
text-rendering: optimizeLegibility;
-moz-osx-font-smoothing: grayscale;
font-feature-settings: "liga";
vertical-align: top;
margin-top: 5px;
}

